html,
body,
#root,
.layout {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans KR', 'Apple SD Gothic Neo', '맑은 고딕', 'Malgun Gothic', sans-serif;
  letter-spacing: -0.4px;
}

.layout {
  display: flex;
  flex-direction: column;
}

.feed-wrap {
  margin-right: auto;
  margin-left: auto;
  width: 1136px;
  max-width: 100%;
  box-sizing: border-box;
  min-height: 1px;
  margin-top: 40px;
}

.feed-row {
  /* height: 500px; */
  display: flex;
  /* flex-wrap: wrap; */
  box-sizing: border-box;
}

.feed-sidebar {
  height: 100%;
  padding-right: 10px;
  padding-left: 10px;
}

.feed-content {
  /* width: 100%; */
  width: 800px;
}
.ant-layout-sider {
  background: white;
}

.category-content {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.bread-crumb {
  margin-bottom: 20px;
}

.carosel-wrapper {
  margin-bottom: 30px;
  height: 260px;
}
