  box-sizing: border-box;
  margin: 0 auto;
  align-items: center;
}

.header-navigation-left {
  flex: 0 0 auto;
  align-self: center;
  top: 50%;
  left: 50%;
}

.header-navigation-right {
  flex: 1 0 auto;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  align-items: center;
  right: 8px;
}

.header-navigation-bar-search {
  display: block;
  flex: 0 1 266px;
  margin: 13px 10px 7px 0;
  min-width: 0;
}

.header-navigation-search {
  position: relative;
}

.header-navigation-search-header {
  display: flex;
}

.header-navigation-search-box {
  flex: 1 0 auto;
}

.header-navigation-icon {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
  display: inline-block;
  margin: 7px 1px;
  padding: 6px;
  box-sizing: border-box;
  width: 36px;
  height: 36px;
  background: transparent;
  border: none;
  cursor: pointer;
  font-family: inherit;
  transition: background 0.1s;
  border-radius: 100%;
}

.header-navigation-icon .icon svg {
  position: absolute;
  top: 6px;
  left: 6px;
  transition: color 0.1s;
  color: #757575;
}

.header-navigation-icon:hover {
  background-color: #35c5f0;
}

.header-navigation-icon:hover .icon {
  color: #fff;
}

.header-navigation-icon .icon {
  height: 24px;
  transition: color 0.1s;
  color: #757575;
}

.header-navigation-bar-login-item:first-child::before {
  display: none;
}

.header-navigation-bar-login-item::before {
  content: '';
  position: absolute;
  top: 6px;
  right: 100%;
  bottom: 3px;
  margin-right: 8px;
  border-left: 1px solid #757575;
}

.header-navigation-user-drop-down {
  position: relative;
}

